import React, { useContext } from 'react'
import logo from "../../images/freshcart-logo.svg"
import { Link, NavLink } from 'react-router-dom'
import { LoginContext } from "../../ShareData/LoginContext";
import { useNavigate } from 'react-router-dom';
import { CartContext } from '../../ShareData/CartContext';
export default function Navbar() {
  let{userToken,setToken}=useContext(LoginContext)
let{ numOfCartItems}=useContext(CartContext)
  let navg =useNavigate()
  function logout(){
    setToken(" ")
    localStorage.removeItem("userToken")
    navg('./Login')
  }
 
  
  console.log(userToken);
  return (
    <nav className="navbar fixed-top navbar-expand-lg bg-body-tertiary ">
    <div className="container ">
      <Link className="navbar-brand" to='home'>
        <img src={logo} alt="" />
      </Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse " id="navbarSupportedContent">
    
    {(userToken!=" ") ? <> <ul className="navbar-nav me-auto mb-2 mb-lg-0  ">

<li className="nav-item">
  <NavLink className="nav-link " to="home">Home</NavLink>
</li>

<li className="nav-item">
  <NavLink className="nav-link " to="/categories">Category</NavLink>
</li>
<li className="nav-item">
  <NavLink className="nav-link " to="/Brands">Brands</NavLink>
</li>
<li className="nav-item">
  <NavLink className="nav-link " to="/cart">Cart</NavLink>
</li>
<li className="nav-item">
  <NavLink className="nav-link " to="/allorders">All Orders</NavLink>
</li>
<li className="nav-item">
  <NavLink className="nav-link " to="/Wishlist">Wish List</NavLink>
</li>


</ul>  </>:""}
     


        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
          <li className="nav-item d-flex align-items-center">
            <i className='fa-brands mx-2 fa-2x fa-instagram cursor-pointer '></i>
            <i className='fa-brands mx-2 fa-2x fa-youtube cursor-pointer'></i>
            <i className='fa-brands mx-2 fa-2x fa-twitter cursor-pointer'></i>
            <i className='fa-brands mx-2 fa-2x fa-spotify cursor-pointer'></i>
          </li>
       

          {(userToken==" ") ? "":<>    <li className="nav-item">
              <NavLink to='/cart'>
                <span className="nav-link cursor-pointer position-relative">
                  <i className='fa-solid fa-cart-shopping fa-2x'></i>

                  <span className="position-absolute top-0 start-100 translate-middle p-2 text-main border border-light rounded-circle">
                  {numOfCartItems}
                  </span>
                </span>
              </NavLink>
            </li>
          </>}
         
          {(userToken==" ") ? <> <li className="nav-item">
              <NavLink className="nav-link" to="/login">login</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/">Register</NavLink>
            </li></>:<> <li className="nav-item">
                <span className="nav-link cursor-pointer" onClick={logout} >Logout</span>
              </li> </>}
           
      




        </ul>
      </div>
    </div>
  </nav>
  )
}
