import axios from "axios";
import React, { useContext, useState } from "react";
import { useQuery } from "react-query";
import MainSilder from "../MainSilder/MainSilder";
import CategorySlider from "../CategorySlider/CategorySlider";
import { CartContext } from "../../ShareData/CartContext";
import toast, { Toaster } from "react-hot-toast";
import { WishContext } from './../../ShareData/WishContext';
import { Link } from "react-router-dom";

export default function Home() {
  let{addWishList}=useContext(WishContext)
  let [page, setPage] = useState(1);
  let { addCart, setnumOfCartItems } = useContext(CartContext);

  function getProducts(queryParm) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products?page=${queryParm.queryKey[1]}`
    );
  }
  let { isLoading, data } = useQuery(["productApi", page], getProducts);
  console.log(data);

  async function addToCart(id) {
    let req = await addCart(id);
    if (req.data.status == "success") {

      setnumOfCartItems(req.data.numOfCartItems)
      toast.success(req.data.message, {

      });
    }
    console.log(req);
  }

  
  async function addToWishList(e,id){
    let req = await addWishList(id);
    e.target.classList.replace('fa-regular','fa-solid')
  }
  return (
    <>
      <Toaster />
      {isLoading ? <div className='loading position-absolute  top-0 end-0 start-0 bottom-0 d-flex justify-content-center align-items-center bg-white ' style={{height:"100vh"}}>
        <span className="loader"></span>

      </div> :<>   <div className="container">
        <MainSilder />
        <CategorySlider />
        <div className="row g-3">
          {data?.data?.data.map((product) => {
            return (
              <>
                <div className="col-md-3 rounded-1 product position-relative">
                  
                  
                    <Link className="text-decoration-none" to={`/ProductDetails/${product.id}`}>
                      <div>
                  <img src={product.imageCover} className="w-100" alt="" />
                  <h5 className="text-main text-decoration-none">{product.category.name}</h5>
                  <h5 className=" text-dark">{product.title.slice(" ").split(0, 2).join(" ")}</h5>
                  <div className="d-flex justify-content-between">
                    <p className="text-decoration-none text-dark">{product.price} EGP </p>
                    <i className=" fa-solid fa-star rating-color ">
                      {" "}
                      {product.ratingsAverage}
                    </i>
                  </div>
                  </div>
                  </Link>
                  
                   <i onClick={ (e)=>addToWishList(e,product.id)} className='fa-regular fa-heart fa-2x position-absolute top-0 end-0 m-3 text-danger'></i>

                  <button
                    onClick={() => addToCart(product.id)}
                    className="btn bg-main w-100 text-light"
                  >
                    <h4>Add</h4>
                  </button>
                </div>
              </>
            );
          })}
        </div>
      </div>

      <nav aria-label="Page  navigation example">
        <ul className="pagination mt-5 justify-content-center">
          <li className="page-item">
            <a className="page-link" href="#" aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>

          <li className="page-item">
            <a className="page-link" onClick={() => setPage(1)}>
              1
            </a>
          </li>
          <li className="page-item">
            <a className="page-link" onClick={() => setPage(2)}>
              2
            </a>
          </li>
          <li className="page-item">
            <a className="page-link" href="#" aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
      </nav></>}
   
    </>
  );
}
