import axios from 'axios'
import { createContext, useState } from 'react'

export let WishContext = createContext()

export function WishContextProvider({ children }) {
    
let [wishId,setWishId]=useState([])

   
    function addWishList(id) {
        let options = {
            headers: {
                token: localStorage.getItem("userToken")
            }
        }
        let body = {
            productId: id
        }
        return axios.post('https://ecommerce.routemisr.com/api/v1/wishlist', body, options)
    }

 
    function removeWishList(id) {
        let options = {
            headers: {
                token: localStorage.getItem("userToken")
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${id}`, options)
    }


    return <WishContext.Provider value={{  removeWishList, addWishList }}>
        {children}
    </WishContext.Provider>
}