import axios from 'axios';
import React, { useState } from 'react'
import { useQuery } from 'react-query';

export default function Brands() {
  let [page, setPage] = useState(1);
  function getBrandss(queryParm) {
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/brands?page=${queryParm.queryKey[1]}`
    );
  }
  let { isLoading, data } = useQuery(["Brands", page], getBrandss);
  console.log(data);
  return (
   
    <>
        <div className="container p-5 ">
        {isLoading ? (
          <div className="loading position-absolute end-0 start-0 bottom-0 d-flex justify-content-center align-items-center bg-white ">
            <span className="loader"></span>
          </div>
        ) : (
          <>
          <h1 className='border-bottom p-5 mb-3 text-main text-center'>Our Brands</h1>
            <div className="row g-3">
              {data?.data?.data.map((Brand) => {
                return <>
                <div className="col-md-3 product border-1 p-2">
                
                <img src={Brand.image} alt="" className=" w-100 border-2 p-1" style={{height:"80%"}} />

                
                <h2 className="text-main text-center  border-1" style={{height:"10%"}}>{Brand.name}</h2>
              </div>
                </>;
              })}

              
            </div>
            <nav aria-label="Page  navigation example">
        <ul className="pagination mt-5 justify-content-center">
          <li className="page-item">
            <a className="page-link" href="#" aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
            </a>
          </li>

          <li className="page-item">
            <a className="page-link" onClick={() => setPage(1)}>
              1
            </a>
          </li>
          <li className="page-item">
            <a className="page-link" onClick={() => setPage(2)}>
              2
            </a>
          </li>
          <li className="page-item">
            <a className="page-link" href="#" aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
            </a>
          </li>
        </ul>
      </nav>
          </>
        )}
      </div>
    
   
    </>
  )
}
