import React, { useContext, useEffect } from "react";
import axios from "axios";
import { useQuery } from "react-query";
import { LoginContext } from "../../ShareData/LoginContext";
import { jwtDecode } from "jwt-decode";

export default function Allorders() {
  let { userToken } = useContext(LoginContext);
  let tok = jwtDecode(userToken);
  function getUserOrder() {
    let options = {
      headers: {
        token: localStorage.getItem("userToken"),
      },
    };
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/orders/user/${tok.id}`,
      options
    );
  }
  let { isLoading, data } = useQuery(["orderApi"], getUserOrder);
  console.log(data);

  return (
    <>
      {isLoading ? (
        <div
          className="loading position-absolute top-0  end-0 start-0 bottom-0 d-flex justify-content-center align-items-center bg-white "
          style={{ height: "100vh" }}
        >
          <span className="loader"></span>
        </div>
      ) : (
        <>
         {userToken === null ? (
        <>
          <div className="d-flex justify-content-center align-items-center fixed top-0 bottom-0 start-0 end-0">
            <i class="fa-solid fa-spinner fa-spin-pulse text-main"></i>
          </div>
        </>
      ) : (
        <>
          {" "}
          <div className="container border-bottom p-3  ">
            <h1 className="text-center text-main border-bottom  pb-5">
              All your orders
            </h1>
            {data?.data.map((item) => {
              return (
                <div className="mt-5 ">
                  <div className="bg-main d-flex rounded-1 justify-content-between container">
                    <h2 className=" ">{item.user.name}</h2>
                    <h2 className="">total price : {item.totalOrderPrice}</h2>
                  </div>
                  <table className="table-success w-100 container text-center">
                    <thead>
                      <tr className="container  bg-success d-flex justify-content-around">
                        <th>count</th>
                        <th>name</th>
                        <th>price</th>
                      </tr>
                    </thead>
                    <tbody>
                      {item.cartItems.map((order) => {
                        return (
                          <tr className="container  bg-success d-flex justify-content-around">
                            <td className="pt-2  " style={{ width: "33%" }}>
                              {order.count}{" "}
                            </td>
                            <td className="pt-2" style={{ width: "33%" }}>
                              {order.product.title
                                .slice(" ")
                                .split(0, 2)
                                .join(" ")}
                            </td>
                            <td className="pt-2" style={{ width: "33%" }}>
                              {order.price * order.count}
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              );
            })}
          </div>{" "}
        </>
      )}</>
      )}
     
    </>
  );
}
