import axios from "axios";
import React from "react";
import { useQuery } from "react-query";

export default function Categories() {
  function getCategories() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
  }
  let { data, isLoading } = useQuery(["category"], getCategories);
  console.log(data);
  return (
    <>
      
        {isLoading ? (
          <div className="loading position-absolute top-0  end-0 start-0 bottom-0 d-flex justify-content-center align-items-center bg-white " style={{height:"100vh"}}>
            <span className="loader"></span>
          </div>
        ) : (
          <>
          <div className="container p-5 ">
            <div className="row g-3">
              {data?.data?.data.map((category) => {
                return <>
                <div className="col-md-3 product border-1 p-2">
                
                <img src={category.image} alt="" className=" w-100 border-2 p-1" style={{height:"80%"}} />

                
                <h2 className="text-main text-center pt-5 border-1" style={{height:"10%"}}>{category.name}</h2>
              </div>
                </>;
              })}

              
            </div>
            </div>
          </>
        )}
   
    </>
  );
}
