import React, { useContext, useState } from "react";

import { Link } from "react-router-dom";
import { WishContext } from "../../ShareData/WishContext";
import { useQuery } from "react-query";
import axios from "axios";

export default function WishList() {
  let [load, setload] = useState(true);
  let {  removeWishList } = useContext(WishContext);
  let [WishListData, setWishListData] = useState(null);

  function getUserWishList() {
    let options = {
        headers: {
            token: localStorage.getItem("userToken")
        }
    }
    return axios.get('https://ecommerce.routemisr.com/api/v1/wishlist', options)
}
  let { isLoading, data } = useQuery(["productApi"], getUserWishList ,{refetchInterval:500});
  console.log(data);
  
    
    
    

  async function removeItemWishList(id) {
    let req = await removeWishList(id);
    console.log(req);
   
  }

  return (
    <>
      <div className=" container p-5 position">
        <h1 className="  text-center text-main border-bottom p-3">Wish Data</h1>
      </div>

      {isLoading ? (
 <div
 style={{ height: "100vh" }}
 className="loading position-absolute top-0 end-0 start-0 bottom-0 d-flex justify-content-center align-items-center bg-white "
>
 <span className="loader"></span>
</div>      ) : (
        <div className="container ">
           <div className="clearfix"></div>
          {data?.data?.data.map((el) => {
            return (
              <div className="row py-3 border-bottom border-3 align-items-center">
                <div className="col-md-10 d-block">
                  <div className="row align-items-center">
                    <div className="col-md-1">
                      <img src={el.imageCover} className="w-100" alt="" />
                    </div>
                    <div className="col-md-10">
                      <h6>{el.title}</h6>
                      <h5 className="text-muted">Price:{el.price}EGP</h5>
                      <button
                        onClick={() => removeItemWishList(el.id)}
                        className="btn btn-danger btn-sm"
                      >
                        Remove <i className="fa-solid fa-trash"></i>
                      </button>
                    </div>
                  </div>
                </div>
                <div className="col-md-2">
                  <i
                    id={"spinner" + el.id}
                    className=" d-none fa-solid fa-spinner fa-spin text-main"
                  ></i>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </>
  );
}
